<template>
  <div class="tax-page apis">
    <h4>API管理<span>/</span>修改API</h4>
    <div class="content">
      <el-form ref="inputForm" :model="api" :rules="rules" label-width="120px">
        <el-form-item label="Api名称" prop="name">
          <el-input v-model="api.name" placeholder="请输入Api名称"></el-input>
          <div class="form-tip">请填写区域名称.</div>
        </el-form-item>
        <el-form-item label="Api类型">
          <div class="types">
            <a v-for="a in types" :class="a == api.apiName?'cur':''" :key="a">{{a}}</a>
          </div>
          <div class="form-tip">类型不能修改.</div>
        </el-form-item>
        <el-form-item label="Api价格" prop="price">
          <el-input v-model="api.price" placeholder="请输入Api价格"></el-input>
          <div class="form-tip">每年的价格，如果填入0则表示免费.</div>
        </el-form-item>
        <el-form-item label="Api介绍" prop="descs">
          <el-input v-model="api.descs" type="textarea" :rows="10" placeholder="请输入Api介绍"></el-input>
          <div class="form-tip">填入api介绍信息，让用户能知道其能提供的服务.</div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">提交</el-button>
          <swak-link class="el-button" to="/system/area">返回</swak-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import ServiceApi from '@/api/system/api';

  export default {
    name: "apiAdd",
    data() {
      return {
        types:['计算个税','个税还原','个税筹划'],
        api: {},
        rules: {
          name: [
            {required: true, message: '请输入API名称', trigger: 'blur'},
          ],
          price : [
            {required: true, message: '请输入API价格', trigger: 'blur'},
          ],
          descs : [
            {required: true, message: '请输入API介绍', trigger: 'blur'},
          ]
        }
      }
    },
    mounted() {
      this.$emit('layout:load', "apis")
      var id = this.$route.params.id;
      this.loadApi(id);
    },
    methods: {
      loadApi: function (id) {
        if (!id) {
          return;
        }
        ServiceApi.get(id).then(res => {
          this.api = res.obj || []
        })
      },
      onSubmit: function () {
        this.$refs['inputForm'].validate(res => {
          if (res) {
            ServiceApi.save(this.api).then(res => {
              this.$router.replace("/system/apis");
              this.$notify.success("保存API成功!");
            });
          } else {
            this.$notify.error("表单错误");
          }
        });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../ui/common/stylus/variable.styl"
</style>